import Vue from 'vue'
import './plugins/vuetify'
import App from './App.vue'
import Vuetify from 'vuetify'
import router from './router'
import store from './store'
import 'vuetify/dist/vuetify.min.css'
import './registerServiceWorker'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faUndo, faLink, faAlignLeft, faSuperscript, faBold, faItalic, faRedo, faUnderline } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faUndo, faLink, faAlignLeft, faSuperscript, faBold, faItalic, faRedo, faUnderline)


Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.use(Vuetify)
Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
